.App {
  padding:50px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size:14px;
}

.App-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.movie {
  background-color:white;
  width:40%;
  display: flex;
  justify-content: space-between;
  align-items:flex-start;
  flex-wrap:wrap;
  margin-bottom:50px;
  text-overflow: ellipsis;
  padding:0 20px;
  box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133, 133, 133,0.22);
}

.movie-column {
  width:30%;
  box-sizing:border-box;
  text-overflow: ellipsis;
}

.movie-column:last-child {
  padding:20px 0;
  width:60%;
}

.movie h1 {
  font-size:20px;
  font-weight: 600;
}

.movie .movie-genres {
  display: flex;
  flex-wrap:wrap;
  margin-bottom:20px;
}

.movie-genres .movie-genre {
  margin-right:10px;
  color:#B4B5BD;
}

.movie .movie-synopsis {
  text-overflow: ellipsis;
  color:#B4B5BD;
  overflow: hidden;
}

.movie .movie-poster {
  max-width: 100%;
  position: relative;
  top:-20px;
  box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

@media screen and (min-width:320px) and (max-width:667px){
  .movie {
      width:100%;
  }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
  .movie {
      width:100%;
      flex-direction: column;
  }
  .movie-poster {
      top:0;
      left:0;
      width:100%;
  }
  .movie-column {
      width:100%!important;
  }
}
